button {
  cursor: pointer;
}

.ma__button,
.ma__button-alt,
.ma__button--minor,
.ma__button-minor,
.ma__button-minor-alt {
  @include ma-button("normal");
}

.ma__button--small,
.ma__button-alt--small,
.ma__button-minor--small,
.ma__button-minor-alt--small {
  @include ma-button("small");
}
